.split .small-cta {
  font-size: 0.85em;
}
.small-cta .small-cta-btn {
  white-space: nowrap;
}
.small-cta .small-cta-btn:before {
  font-family: 'squarespace-ui-font';
  font-style: normal;
  font-weight: normal;
